.featured-products-title {
  font-weight: 900;
  padding: 0;
}
@media (max-width: 640px) {
  .featured-products-title {
    font-size: 2rem;
    margin: 40px 0 0 0;
  }
}
@media (min-width: 641px) and (max-width: 1023px) {
  .featured-products-title {
    font-size: 3rem;
    margin: 24px 0 32px 0;
  }
}
@media (min-width: 1024px) {
  .featured-products-title {
    font-size: 3.75rem;
    margin: 70px 0 40px 0;
  }
}

.featured-products-list {
  display: flex;
  list-style-type: none;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}
@media (max-width: 640px) {
  .featured-products-list {
    width: 100%;
    margin: 0;
    flex-direction: column;
  }
}
@media (min-width: 641px) and (max-width: 1023px) {
  .featured-products-list {
    flex-direction: column;
    width: 100%;
    margin: 0;
  }
}
@media (min-width: 1024px) {
  .featured-products-list {
    width: calc(100% + 96px);
    margin: 0 -48px;
    flex-direction: row;
  }
}

.featured-products-item {
  display: flex;
  flex-direction: column;
  position: relative;
}
@media (max-width: 640px) {
  .featured-products-item {
    width: 100%;
    padding: 40px 0;
  }
}
@media (min-width: 641px) and (max-width: 1023px) {
  .featured-products-item {
    width: 100%;
    padding: 0 0 48px;
    margin: 0 0 48px;
  }
}
@media (min-width: 1024px) {
  .featured-products-item {
    padding: 0 48px;
    width: 25%;
  }
}
.featured-products-item:last-child .featured-product-link::after {
  display: none;
}

.featured-product-link {
  display: flex;
  width: 100%;
  transition: background-color 1s linear;
  line-height: 0;
}
.featured-product-link::after {
  content: "";
  position: absolute;
  background-color: #e5e5e5;
  height: 1px;
}
@media (max-width: 640px) {
  .featured-product-link::after {
    flex-direction: row;
    bottom: 0;
    left: 0;
    right: 0;
  }
}
@media (min-width: 641px) and (max-width: 1023px) {
  .featured-product-link::after {
    flex-direction: row;
    bottom: 0;
    left: 0;
    right: -32px;
  }
}
@media (min-width: 1024px) {
  .featured-product-link::after {
    display: none;
  }
}
.featured-product-link:hover {
  background-color: #f0f2ec;
}
.featured-product-link:hover .featured-product-preudolink {
  color: #000;
}
.featured-product-link:active {
  background-color: #f0f2ec;
}
.featured-product-link .block-animation {
  transition-duration: 2s;
}
@media (max-width: 640px) {
  .featured-product-link {
    flex-direction: column;
  }
}
@media (min-width: 641px) and (max-width: 1023px) {
  .featured-product-link {
    flex-direction: row;
  }
}
@media (min-width: 1024px) {
  .featured-product-link {
    flex-direction: column;
  }
}

.featured-product-img {
  max-width: 100%;
  box-shadow: 8px 8px 16px rgba(0, 0, 0, 0.18);
}

@media (max-width: 640px) {
  .featured-product-img-wrapper {
    width: calc(100% - 9px);
  }
}
@media (min-width: 641px) and (max-width: 1023px) {
  .featured-product-img-wrapper {
    width: 200px;
  }
}

.featured-product-descr {
  display: flex;
  flex-direction: column;
  text-align: left;
  justify-content: flex-start;
  align-items: flex-start;
}
@media (max-width: 640px) {
  .featured-product-descr {
    width: 100%;
  }
}
@media (min-width: 641px) and (max-width: 1023px) {
  .featured-product-descr {
    width: calc(100% - 200px);
    padding: 0 0 0 48px;
  }
}
@media (min-width: 1024px) {
  .featured-product-descr {
    width: 100%;
  }
}

.featured-product-title {
  display: flex;
  font-size: 1.5rem;
  line-height: 2;
  font-weight: 700;
  color: #181818;
  padding: 0;
}
@media (max-width: 640px) {
  .featured-product-title {
    margin: 12px 0 0 0;
  }
}
@media (min-width: 641px) and (max-width: 1023px) {
  .featured-product-title {
    margin: -12px 0 0 0;
  }
}
@media (min-width: 1024px) {
  .featured-product-title {
    margin: 14px 0 0 0;
  }
}

.featured-product-subtitle {
  display: flex;
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 500;
  color: #181818;
  padding: 0;
}
@media (max-width: 640px) {
  .featured-product-subtitle {
    word-spacing: 0.6rem;
    margin: 0 0 8px 0;
  }
}
@media (min-width: 641px) and (max-width: 1023px) {
  .featured-product-subtitle {
    word-spacing: 0.5rem;
    margin: 12px 0 8px 0;
  }
}
@media (min-width: 1024px) {
  .featured-product-subtitle {
    word-spacing: 0.5rem;
    margin: 0 0 8px 0;
  }
}

.featured-product-text {
  display: block;
  max-height: 10.2rem;
  font-size: 1rem;
  line-height: 1.7;
  font-weight: 300;
  color: #181818;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media (max-width: 640px) {
  .featured-product-text {
    word-spacing: 0.1rem;
  }
}
@media (min-width: 641px) and (max-width: 1023px) {
  .featured-product-text {
    word-spacing: 0.2rem;
  }
}
@media (min-width: 1024px) {
  .featured-product-text {
    word-spacing: 0.1rem;
  }
}

.featured-product-preudolink {
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 500;
  text-decoration: underline;
  color: #3546a6;
  padding: 0;
  margin: 16px 0 0 0;
}

html {
  -webkit-text-size-adjust: 100%;
  -webkit-overflow-scrolling: touch;
  word-break: break-word;
}

* {
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
  outline: none;
  padding: 0;
  margin: 0;
  transform-origin: center;
}

*:focus {
  outline: none !important;
}

html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  color: #000;
  background-color: #fff;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
}

a {
  appearance: none;
  background-color: transparent;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

[type=button],
[type=reset],
[type=submit],
button {
  -webkit-appearance: button;
}

[type=button]:not(:disabled),
[type=reset]:not(:disabled),
[type=submit]:not(:disabled),
button:not(:disabled) {
  cursor: pointer;
}

button {
  -webkit-appearance: button;
  text-rendering: auto;
  word-spacing: normal;
  text-indent: 0px;
  text-shadow: none;
  cursor: default;
  box-sizing: border-box;
}

.container {
  width: 100%;
  min-width: 375px;
  max-width: 1632px;
  margin: 0 auto;
}
@media (max-width: 640px) {
  .container {
    padding-left: 40px;
    padding-right: 40px;
  }
}
@media (min-width: 641px) and (max-width: 1023px) {
  .container {
    padding-left: 40px;
    padding-right: 40px;
  }
}
@media (min-width: 1024px) {
  .container {
    padding-left: 96px;
    padding-right: 96px;
  }
}

.main-content {
  flex: 1 0 auto;
  padding: 0;
}

.footer {
  flex-shrink: 0;
}